<template>
  <div>
    <div>
      <a-card hoverable style="width: 180px">
        <img :src="imgurl" class="image" />
      </a-card>
      <div class="word">
        <h2>
          {{ head }}
        </h2>
        <p></p>
        <div class="author">
          <a-avatar
            :src="authorUrl"
          />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <span>{{ writer }}</span>
        </div>
        <p></p>
        <div class="tag">
          <el-row>
            <el-button size="mini">播放全部</el-button>
            <el-button size="mini">收藏</el-button>
            <el-button size="mini">分享</el-button>
            <el-button size="mini">下载全部</el-button>
          </el-row>
        </div>
        <p></p>
        <div>
          标签：<a v-for="(item, index) in tag" :key="index"> {{ item }}/ </a>
        </div>
        <p></p>
        <p>简介：{{ brief }}</p>
      </div>
    </div>
    <br />
    <div>
      <a-tabs>
        <a-tab-pane key="1" tab="歌曲列表">
          <div>
            <a-table
              :columns="columns"
              :data-source="data"
              :rowKey="rowkey"
              :customRow="customClick"
            >
            </a-table>
          </div>
        </a-tab-pane>
        <a-tab-pane key="2" tab="评论（1048）"> Content of tab 2 </a-tab-pane>
        <a-tab-pane key="3" tab="收藏者"> Content of tab 3 </a-tab-pane>
        <a-button slot="tabBarExtraContent"> Extra Action </a-button>
      </a-tabs>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import bofang from "@/components/bofang.vue";
const columns = [
  {
    title: "音乐标题",
    dataIndex: "name",
  },
  {
    title: "歌手",
    dataIndex: "ar[0].name",
  },
  {
    title: "专辑",
    dataIndex: "al.name",
  }
];

const data = [];

export default {
  name: "musicDetail",
  components: {
    bofang,
  },
  data() {
    return {
      imgurl: "",
      head: "",
      authorUrl: "",
      writer: "",
      tag: "",
      brief: "",
      data,
      columns,
      loading: false,
    };
  },
  created() {
    console.log(this.$route.params.musicDetailId);
    let musicDetailId = this.$route.params.musicDetailId;
    axios
      .get("https://api.mtnhao.com/playlist/detail?id=" + musicDetailId)
      .then((res) => {
        console.log("res", res);
        this.imgurl = res.data.playlist.coverImgUrl;
        this.head = res.data.playlist.name;
        this.authorUrl = res.data.playlist.creator.avatarUrl;
        this.writer = res.data.playlist.creator.nickname;
        this.tag = res.data.playlist.tags;
        this.brief = res.data.playlist.description;
        console.log(res.data.playlist.tracks);
        this.data = res.data.playlist.tracks;
        console.log();
      })
      .catch((err) => {
        console.log(error);
      });
  },
  methods: {
    onSelectChange(selectedRowKeys) {
      console.log("selectedRowKeys changed: ", selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },
    rowkey(record) {
      return record.id;
    },
    click(record) {
      console.log("11111", this);
      this.$router.push("/bofang/" + record.id);
    },
    customClick(record) {
      console.log(this);

      return {
        on: {
          click: () => {
            this.click(record);
          },
        },
      };
    },
  },
};
</script>
<style scoped>
.image {
  width: 100%;
}
.word {
  width: 500px;
  height:38vh;
  margin-left: 200px;
  margin-top: -170px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
}
.word h2 {
  float: left;
}
.word .tag {
  float: left;
}
</style>