<template>
  <el-carousel :interval="4000" height='32vh'  type='card' :autoplay="true">
    <el-carousel-item v-for="item in imagesbox" :key="item.id" >
      <img :src="item.pic" width="100%" />
    </el-carousel-item>
  </el-carousel>
</template>


<style>
 
</style>



<script>
import axios from "axios";
export default {
 

  data() {
    return {
      imagesbox: [],
    };
  },
  created() {
    axios
      .get("https://api.mtnhao.com/banner?type=2")
      .then((res) => {
        console.log(res);
        this.imagesbox = res.data.banners;
      })
      .catch((err) => {
        console.log(error);
      });
  },
};
</script>