<template>

  <div>
    <a-card hoverable style="width: 300px">
      <img :src="imgurl" class="image" /> 
      <p></p>
      <h2>歌曲名字：{{name}}</h2>
      <h2>歌手：{{zuozhe}}</h2>
    </a-card>
    <div class="lyric">
      <p>{{lyric}}</p>
    </div>
    <div>
      <audio
        slot="cover"
        ref="audio"
        @pause="onPause"
        @play="onPlay"
        @timeupdate="onTimeupdate"
        @loadedmetadata="onLoadedmetadata"
        :src="'http://music.163.com/song/media/outer/url?id=' + musicId"
        controls="controls"
      ></audio>
    </div>
    

    <div>
      <el-button type="button" @click="startPlayOrPause">{{
        audio.playing | transPlayPause
      }}</el-button>
      <el-tag type="info">{{ audio.currentTime | formatSecond }}</el-tag>
      <el-tag type="info">{{ audio.maxTime | formatSecond }}</el-tag>
    </div>
    <div>
      <el-slider
        v-model="sliderTime"
        :format-tooltip="formatProcessToolTip"
        @change="changeCurrentTime"
        class="slider"
      ></el-slider>
    </div>
  </div>
</template>

<script>
import axios from "axios";


function realFormatSecond(second) {
  var secondType = typeof second;

  if (secondType === "number" || secondType === "string") {
    second = parseInt(second);

    var hours = Math.floor(second / 3600);
    second = second - hours * 3600;
    var mimute = Math.floor(second / 60);
    second = second - mimute * 60;

    return (
      hours + ":" + ("0" + mimute).slice(-2) + ":" + ("0" + second).slice(-2)
    );
  } else {
    return "0:00:00";
  }
}
export default {
  data() {
    return {
      sliderTime: 0,
      imgurl: "",
      name:'',
      zuozhe:'',
      lyric:'',
      audio: {
        // 该字段是音频是否处于播放状态的属性
        playing: false,
        // 音频当前播放时长
        currentTime: 0,
        // 音频最大播放时长
        maxTime: 0,
      },
      musicId: "",
    };
  },
  created() {
    console.log(this.$route.params.musicId);
    let musicId = this.$route.params.musicId;
    this.musicId = this.$route.params.musicId;

    axios
      .get("https://api.mtnhao.com/song/detail?ids=" + musicId)
      .then((res) => {
        console.log(res);
        console.log(res.data.songs[0].al.picUrl);
        console.log("name", res.data.songs[0].al.name);
        console.log("作者", res.data.songs[0].ar[0].name);
        this.imgurl = res.data.songs[0].al.picUrl;
        this.name = res.data.songs[0].al.name
        this.zuozhe = res.data.songs[0].ar[0].name
      })
      .catch((err) => {
        console.log(error);
      });
    axios
      .get("https://api.mtnhao.com/lyric?id=" + musicId)
      .then((res) => {
        console.log('歌词',res)         
        this.lyric = res.data.lrc.lyric

            
      })
      .catch((err) => {
        console.log(error);
      });
  },
  methods: {
    startPlayOrPause() {
      return this.audio.playing ? this.pause() : this.play();
    },
    play() {
      this.$refs.audio.play();
    },
    pause() {
      this.$refs.audio.pause();
    },
    onPlay() {
      this.audio.playing = true;
    },
    onPause() {
      this.audio.playing = false;
    },
    onTimeupdate(res) {
      console.log("timeupdate");
      console.log(res);
      this.audio.currentTime = res.target.currentTime;
    },
    onLoadedmetadata(res) {
      console.log("loadedmetadata");
      console.log(res);
      this.audio.maxTime = parseInt(res.target.duration);
    },

    changeCurrentTime(index) {
      this.$refs.audio.currentTime = parseInt(
        (index / 100) * this.audio.maxTime
      );
    },
    onTimeupdate(res) {
      console.log("timeupdate");
      console.log(res);
      this.audio.currentTime = res.target.currentTime;
      this.sliderTime = parseInt(
        (this.audio.currentTime / this.audio.maxTime) * 100
      );
    },
    formatProcessToolTip(index = 0) {
      index = parseInt((this.audio.maxTime / 100) * index);
      return "进度条: " + realFormatSecond(index);
    },
  },
  filters: {
    transPlayPause(value) {
      return value ? "暂停" : "播放";
    },
    formatSecond(second = 0) {
      return realFormatSecond(second);
    },
  },
};
</script>

<style scoped>
.image {
  width: 100%;
}
.lyric{
  width: 780px;
  float: right;
  margin-top: -360px;
  margin-left: 5px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);  
}
</style>
