import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import kuaidi from '@/components/kuaidi.vue'
const lunbo=()=>import("@/components/lunbotu.vue")
const music=()=>import("@/components/music.vue")
const musicDetail=()=>import("@/components/musicDetail.vue")
const exclusive=()=>import("@/components/exclusive.vue")
const fanyi=()=>import("@/components/fanyi.vue")
const bofang=()=>import("@/components/bofang.vue")

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path:'/kuaidi',
    component:kuaidi
  },
  {
    path:'/musicDetail/:musicDetailId',
    component:musicDetail
  },
  {
    path:'/fanyi',
    component:fanyi
  },
  {
    path:'/bofang/:musicId',
    component:bofang
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
