<template>
  <div>
    <div class="tou">
      <span>推荐歌单</span>
      <el-divider></el-divider>
    </div>
    <el-row>
        <el-col
          :span="4"
          v-for="(item, index) in imagesbox"
          :key="item.id"
          :offset="index > 0 ? 2 : 2"
        >
          <el-card :body-style="{ padding: '0px' }" shadow="hover" class="card">
            <div  @click="todetails(item.id)">
              <img :src="item.picUrl" class="image" />
              <div style="padding: 14px">
                <span>{{ item.name }}</span>
              </div>
            </div>
          </el-card>
        </el-col>
    </el-row>
  </div>
</template>

<style scoped>
.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "null";
}

.clearfix:after {
  clear: both;
}
.el-col-offset-2 {
  margin-left: 2.5%;
}
.card {
  height: 39vh;
  margin-bottom: 25px;
}
</style>


<script>
import axios from "axios";
import musicDetail from "@/components/musicDetail.vue";
export default {
  data() {
    return {
      imagesbox: [],
    };
  },
  created() {
    axios
      .get("https://api.mtnhao.com/personalized?limit=20")
      .then((res) => {
        console.log(res);
        this.imagesbox = res.data.result;
      })
      .catch((err) => {
        console.log(error);
      });
  },
  methods:{
    todetails(id){
      this.$router.push({path: '/musicDetail/'+id})
    },
  },
};
</script>